export default ()=> {
      return (
        <div className="question" data-index="94" dangerouslySetInnerHTML = {{ __html: `
<h6>94. 输出什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-function">function</span> <span class="token function">getItems</span><span class="token punctuation">(</span><span class="token parameter">fruitList<span class="token punctuation">,</span> <span class="token operator">...</span>args<span class="token punctuation">,</span> favoriteFruit</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
  <span class="token keyword keyword-return">return</span> <span class="token punctuation">[</span><span class="token operator">...</span>fruitList<span class="token punctuation">,</span> <span class="token operator">...</span>args<span class="token punctuation">,</span> favoriteFruit<span class="token punctuation">]</span>
<span class="token punctuation">}</span>

<span class="token function">getItems</span><span class="token punctuation">(</span><span class="token punctuation">[</span><span class="token string">"banana"</span><span class="token punctuation">,</span> <span class="token string">"apple"</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token string">"pear"</span><span class="token punctuation">,</span> <span class="token string">"orange"</span><span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: <code>[&quot;banana&quot;, &quot;apple&quot;, &quot;pear&quot;, &quot;orange&quot;]</code></li>
<li>B: <code>[[&quot;banana&quot;, &quot;apple&quot;], &quot;pear&quot;, &quot;orange&quot;]</code></li>
<li>C: <code>[&quot;banana&quot;, &quot;apple&quot;, [&quot;pear&quot;], &quot;orange&quot;]</code></li>
<li>D: <code>SyntaxError</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: D</h4>
<p><code>... args</code>是剩余参数，剩余参数的值是一个包含所有剩余参数的数组，<strong>并且只能作为最后一个参数</strong>。上述示例中，剩余参数是第二个参数，这是不可能的，并会抛出语法错误。</p>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-function">function</span> <span class="token function">getItems</span><span class="token punctuation">(</span><span class="token parameter">fruitList<span class="token punctuation">,</span> favoriteFruit<span class="token punctuation">,</span> <span class="token operator">...</span>args</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
  <span class="token keyword keyword-return">return</span> <span class="token punctuation">[</span><span class="token operator">...</span>fruitList<span class="token punctuation">,</span> <span class="token operator">...</span>args<span class="token punctuation">,</span> favoriteFruit<span class="token punctuation">]</span>
<span class="token punctuation">}</span>

<span class="token function">getItems</span><span class="token punctuation">(</span><span class="token punctuation">[</span><span class="token string">"banana"</span><span class="token punctuation">,</span> <span class="token string">"apple"</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token string">"pear"</span><span class="token punctuation">,</span> <span class="token string">"orange"</span><span class="token punctuation">)</span>
</code></pre>
<p>上述例子是有效的，将会返回数组：<code>[ 'banana', 'apple', 'orange', 'pear' ]</code></p>
</p>
</details>
` }}></div>
      );
    }